// custom-modal
.custom-modal {
    background-color: rgba(0, 0, 0, .2);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99999; 
    transition: $transition;

    .close-link {
        cursor: pointer;
        line-height: 1;
    }
    .custom-modal-content  {
        width: 400px;
        height: 100%;
        background-color: $body-bg;
        overflow-y: auto;
    }
    .custom-modal-header {
        background-color: $body-bg;
    }

    &.right {
        right: -100%;
        left: auto;

        &.show {
            right: 0;
            left: 0;
        }
        .custom-modal-content  {
            margin-left: auto;
        }
    }
    &.left {
        left: -100%;
        right: auto;

        &.show {
            right: 0;
            left: 0;
        } 
    }
    &.center {
        transform: scale(0);
        padding: 20px;
        
        &.show {
            transform: scale(1);
        }
        .custom-modal-content  {
            width: 600px;
            margin-left: auto;
            margin-right: auto;
        }
    }
    .close-outside {
        position: absolute;
        left: 0;
        top: 0;
        cursor: ne-resize;
        width: 100%;
        height: 100%;
    }
}

/* Marketing Dashboard CSS */
